:root {
  color-scheme: light dark;
  --primary-base: var(--blue-500);
  --primary-text: light-dark(var(--blue-600), var(--blue-400));
  --primary-text-emphasis: light-dark(var(--blue-800), var(--blue-200));
  --primary-bg: var(--blue-500);
  --primary-bg-subtle: light-dark(var(--blue-100), var(--blue-900));
  --primary-bg-muted: light-dark(var(--blue-200), var(--blue-800));
  --primary-border: light-dark(var(--blue-300), var(--blue-600));
  --primary-focus-ring: color-mix(in oklch, light-dark(var(--blue-500), var(--blue-400)) 50%, var(--bg-body));
  --primary-contrast: var(--white);
  --accent-base: var(--indigo-500);
  --accent-text: light-dark(var(--indigo-600), color-mix(in oklch, var(--indigo-400), var(--indigo-300)));
